.TodoHistory {
  margin-bottom: 16px;
  > .dailyTodo {
    padding: 16px;
    border: 1px solid #dddddd;
    border-radius: 8px;
    margin-bottom: 8px;
    display: flex;

    > .summary {
      width: 100px;
      margin-right: 8px;
      > .dateWrapper{
        margin-bottom: 8px;
        > .date {
          color: black;
        }
        > .weekday {
          font-size: 8px;
          color: #888;
        }
      }
      > .finishCount{
       font-size: 8px;
      }
    }

    > .todoList {
      flex: 1;
    }
  }
}
@media screen and (max-width: 700px) {
  .TodoHistory {
    > .dailyTodo {
      display: block;
      > .summary {
        margin-bottom: 5px;
      }
    }
  }
}