#SignUp{
  max-width: 500px;
  margin: auto;
  padding: 50px 0;
  >*{
    margin: 10px 0;
  }
  h1{
    text-align: center;
  }
  > .signUpButton{
    margin: 0 auto;
    display: block;
    border-radius: 6px;
  }
}

@media screen and (max-width: 400px) {
  #SignUp {
    margin: 0 1em;
  }
}
